import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/desc1/bg.jpg'
// import backgroundImage from '../assets/images/sample/03_第一關說明.png'
import startBtn from '../assets/images/start.png';

class Desc1 extends Component {

    btnSound = './sounds/btn_sound.mp3';

    constructor(props) {
        super(props);
    }
    
    goToGame1Page() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './game1',
            state: {}
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {/* <img alt='' src={introImage} style={{ width: '60%', position: 'absolute', left: '2.5%', top: '4%' }}></img> */}
                <img className='press-btn' alt='' src={startBtn} style={{ height: '8.5%', top: '69.5%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToGame1Page.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Desc1);