import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import quizbackgroundImage1 from '../assets/images/game2/quiz1/question.jpg'
import quizbackgroundImage2 from '../assets/images/game2/quiz2/question.jpg'
import quizbackgroundImage3 from '../assets/images/game2/quiz3/question.jpg'
import quizbackgroundImage4 from '../assets/images/game2/quiz4/question.jpg'
import quizbackgroundImage5 from '../assets/images/game2/quiz5/question.jpg'
import answerbackgroundImage1 from '../assets/images/game2/quiz1/answer.jpg'
import answerbackgroundImage2 from '../assets/images/game2/quiz2/answer.jpg'
import answerbackgroundImage3 from '../assets/images/game2/quiz3/answer.jpg'
import answerbackgroundImage4 from '../assets/images/game2/quiz4/answer.jpg'
import answerbackgroundImage5 from '../assets/images/game2/quiz5/answer.jpg'
import quiz1Option1 from '../assets/images/game2/quiz1/01.png'
import quiz1Option2 from '../assets/images/game2/quiz1/02.png'
import quiz1Option3 from '../assets/images/game2/quiz1/03.png'
import quiz2Option1 from '../assets/images/game2/quiz2/01.png'
import quiz2Option2 from '../assets/images/game2/quiz2/02.png'
import quiz2Option3 from '../assets/images/game2/quiz2/03.png'
import quiz3Option1 from '../assets/images/game2/quiz3/01.png'
import quiz3Option2 from '../assets/images/game2/quiz3/02.png'
import quiz3Option3 from '../assets/images/game2/quiz3/03.png'
import quiz4Option1 from '../assets/images/game2/quiz4/01.png'
import quiz4Option2 from '../assets/images/game2/quiz4/02.png'
import quiz4Option3 from '../assets/images/game2/quiz4/03.png'
import quiz5Option1 from '../assets/images/game2/quiz5/01.png'
import quiz5Option2 from '../assets/images/game2/quiz5/02.png'
import quiz5Option3 from '../assets/images/game2/quiz5/03.png'
import nextBtn from '../assets/images/game2/next_quiz.png';
import continueBtn from '../assets/images/continue.png';
import backgroundImage from '../assets/images/sample/08-2遊戲畫面2.png';

import countdown1 from '../assets/images/game2/countdown/1.png';
import countdown2 from '../assets/images/game2/countdown/2.png';
import countdown3 from '../assets/images/game2/countdown/3.png';
import countdown4 from '../assets/images/game2/countdown/4.png';
import countdown5 from '../assets/images/game2/countdown/5.png';
import countdown6 from '../assets/images/game2/countdown/6.png';
import countdown7 from '../assets/images/game2/countdown/7.png';
import countdown8 from '../assets/images/game2/countdown/8.png';
import countdown9 from '../assets/images/game2/countdown/9.png';
import countdown10 from '../assets/images/game2/countdown/10.png';

import correct from '../assets/images/game2/correct.png';
import wrong from '../assets/images/game2/wrong.png';

import scorePanel from '../assets/images/game2/score.png';
import addScore from '../assets/images/game2/add.png'
import removeScore from '../assets/images/game2/remove.png'

const FADE_OUT_SPACING = 0.35;
const OPACITY_INTERVAL = 300;
const ANSWERS = [ 1, 0, 2, 0, 0 ];
const OPTION_IMAGES = [
    [
        quiz1Option1, quiz1Option2, quiz1Option3
    ],
    [
        quiz2Option1, quiz2Option2, quiz2Option3
    ],
    [
        quiz3Option1, quiz3Option2, quiz3Option3
    ],
    [
        quiz4Option1, quiz4Option2, quiz4Option3
    ],
    [
        quiz5Option1, quiz5Option2, quiz5Option3
    ]
]

class Game2 extends Component {

    btnSound = './sounds/btn_sound.mp3';
    countdownSound = './sounds/countdown.mp3';
    correctSound = './sounds/correct.mp3';
    wrongSound = './sounds/wrong.mp3';

    constructor(props) {
        super(props);
        this.state = {
            backgroundImage: quizbackgroundImage1,
            option1: OPTION_IMAGES[0][0],
            option2: OPTION_IMAGES[0][1],
            option3: OPTION_IMAGES[0][2],
            stage: 1,
            playing: false,
            countdown: 10,
            countdownImage: countdown10,
            score1: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score1,
            score: 0,
            answerCorrect: false,
            answerWrong: false,
            nextQuizEnable: false,
            add10Opacity: 0,
            remove2Opacity: 0
        };
    }

    componentDidMount() {
        this.setState({ playing : true });
        this.startCountdown();
    }

    componentWillUnmount() {
        this.setState({ playing : false });
        clearInterval(this.countdownInterval);
        if (this.countdownAudio !== undefined) {
            this.countdownAudio.pause();
        }
        if (this.add20Interval !== null) {
            clearInterval(this.add20Interval);
        }
        if (this.remove10Interval !== null) {
            clearInterval(this.remove10Interval);
        }
    }

    startAdd10() {
        this.playCorrectSound();
        if (this.add10Interval !== null) {
            clearInterval(this.add10Interval);
        }
        this.setState({
            add10Opacity: 1.0
        });
        this.add10Interval = setInterval(() => {
            if (this.state.add10Opacity >= 0) {
                this.setState({
                    add10Opacity: this.state.add10Opacity - FADE_OUT_SPACING
                })
            } else {
                clearInterval(this.add10Interval)
                this.add10Interval = null
            }
        }, OPACITY_INTERVAL);
    }

    startRemove2() {
        this.playWrongSound();
        if (this.remove2Interval !== null) {
            clearInterval(this.remove2Interval);
        }
        this.setState({
            remove2Opacity: 1.0
        });
        this.remove2Interval = setInterval(() => {
            if (this.state.remove2Opacity >= 0) {
                this.setState({
                    remove2Opacity: this.state.remove2Opacity - FADE_OUT_SPACING
                })
            } else {
                clearInterval(this.remove2Interval)
                this.remove2Interval = null
            }
        }, OPACITY_INTERVAL);
    }

    startCountdown() {
        clearInterval(this.countdownInterval);
        if (this.countdownAudio !== undefined) {
            this.countdownAudio.pause();
        }
        this.setState({
            countdown: 10,
            countdownImage: countdown10
        });
        this.countdownAudio = this.playCountdownSound();
        this.countdownInterval = setInterval(() => {
            if (this.state.countdown === 0) {
                this.setState({
                    answerWrong: true,
                    answerCorrect: false,
                    score: this.state.score < 2 ? 0 : this.state.score - 2
                });
                this.startRemove2();
                this.showAnswer();
                clearInterval(this.countdownInterval);
                if (this.countdownAudio !== undefined) {
                    this.countdownAudio.pause();
                }
            } else {
                let countdown = this.state.countdown - 1;
                let countdownImage = countdown === 1 ? countdown1 : (countdown === 2 ? countdown2 : (countdown === 3 ? countdown3 : (countdown === 4 ? countdown4 : (countdown === 5 ? countdown5 : (countdown === 6 ? countdown6 : (countdown === 7 ? countdown7 : (countdown === 8 ? countdown8 : (countdown === 9 ? countdown9 : (countdown === 10 ? countdown10 : countdown1)))))))))
                this.setState({
                    countdown: this.state.countdown - 1,
                    countdownImage: countdownImage
                });
            }
        }, 1 * 1000);
    }

    showAnswer() {
        this.setState({
            backgroundImage: this.state.stage === 1 ? answerbackgroundImage1 : (this.state.stage === 2 ? answerbackgroundImage2 : (this.state.stage === 3 ? answerbackgroundImage3 : (this.state.stage === 4 ? answerbackgroundImage4 : answerbackgroundImage5))),
            nextQuizEnable: true
        });
    }

    nextStage() {
        this.playBtnSound();
        if (this.state.stage < 5) {
            let stage = this.state.stage + 1;
            this.setState({
                stage: stage,
                answerCorrect: false,
                answerWrong: false,
                nextQuizEnable: false,
                option1: OPTION_IMAGES[stage - 1][0],
                option2: OPTION_IMAGES[stage - 1][1],
                option3: OPTION_IMAGES[stage - 1][2],
                backgroundImage: stage === 1 ? quizbackgroundImage1 : (stage === 2 ? quizbackgroundImage2 : (stage === 3 ? quizbackgroundImage3 : (stage === 4 ? quizbackgroundImage4 : quizbackgroundImage5)))
            });
            this.startCountdown();
        } else {
            this.goToResult2Page();
        }
    }

    goToResult2Page() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './result2',
            state: {
                score1: this.state.score1,
                score2: this.state.score
            }
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    playCountdownSound() {
        let btnAudio = new Audio(this.countdownSound);
        btnAudio.play();
        return btnAudio;
    }

    playCorrectSound() {
        let btnAudio = new Audio(this.correctSound);
        btnAudio.play();
    }

    playWrongSound() {
        let btnAudio = new Audio(this.wrongSound);
        btnAudio.play();
    }

    optionClick(index) {
        this.playBtnSound();
        if (!this.state.nextQuizEnable) {
            clearInterval(this.countdownInterval);
            if (this.countdownAudio !== undefined) {
                this.countdownAudio.pause();
            }
            let correct = ANSWERS[this.state.stage - 1] === index;
            this.setState({
                answerCorrect: correct,
                answerWrong: !correct,
                score: correct ? this.state.score + 10 : (this.state.score < 2 ? 0 : this.state.score - 2)
            });
            if (correct) {
                this.startAdd10();
            } else {
                this.startRemove2();
            }
            this.showAnswer();
        }
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img alt='' src={scorePanel} style={{ height: '13%', top: '2%', left: '0%', position: 'absolute', background: 'transparent' }}></img>
                <div style={{ position: 'absolute', left: '7.1%', top: '4.5%', width: '11%', height: '8%', color: '#086940' }}>
                    <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                        <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#ffffff" >{this.state.score}</text>
                    </svg>
                </div>
                <img alt='' src={this.state.option1} style={{ width: '18.8%', left: '15.5%', top: '35%', position: 'absolute' }} onClick={() => this.optionClick(0)}></img>
                <img alt='' src={this.state.option2} style={{ width: '18.8%', left: '40.5%', top: '35%', position: 'absolute' }} onClick={() => this.optionClick(1)}></img>
                <img alt='' src={this.state.option3} style={{ width: '18.8%', left: '65.5%', top: '35%', position: 'absolute' }} onClick={() => this.optionClick(2)}></img>
                <img alt='' src={this.state.countdownImage} style={{ height: '24.5%', top: '4%', right: '3%', position: 'absolute', background: 'transparent' }}></img>
                <img alt='' src={addScore} style={{ position: 'absolute', width: '9.4%', left: '8.7%', top: '12.2%', opacity: this.state.add10Opacity }}></img>
                <img alt='' src={removeScore} style={{ position: 'absolute', width: '9.4%', left: '8.7%', top: '12.2%', opacity: this.state.remove2Opacity }}></img>
                {
                    this.state.answerCorrect && <img alt='' src={correct} style={{ height: '31%', top: '3%', right: '2%', position: 'absolute', background: 'transparent' }}></img>
                }
                {
                    this.state.answerWrong && <img alt='' src={wrong} style={{ height: '31%', top: '3%', right: '2%', position: 'absolute', background: 'transparent' }}></img>
                }
                {
                    this.state.nextQuizEnable && <img className='press-btn' alt='' src={this.state.stage > 4 ? continueBtn : nextBtn} style={{ height: '8.5%', top: '80.5%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={() => this.nextStage()}/>
                }
            </div>
        )
    }
}

export default withRouter(Game2);