import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/result2/bg.jpg'
// import backgroundImage from '../assets/images/sample/10_第二關完成.png'
import continueBtn from '../assets/images/continue.png'
import Cookies from 'universal-cookie';

const cookies = new Cookies();

class Result2 extends Component {

    btnSound = './sounds/btn_sound.mp3';
    completedSound = './sounds/completed.mp3';

    constructor(props) {
        super(props);
        this.state = {
            score1: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score1,
            score2: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score2
        };
    }

    componentDidMount() {
        this.playCompletedSound();
    }

    playCompletedSound() {
        let btnAudio = new Audio(this.completedSound);
        btnAudio.play();
    }
  
    goToEndPage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './end',
            state: {
                score1: this.state.score1,
                score2: this.state.score2
            }
        });
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <div id='result-div' style={{ position: 'absolute', width: '100%', height: '100%', left: '0px', top: '0px' }}>
                    <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                </div>
                <div style={{ position: 'absolute', right: '36%', top: '30%', width: '20%', height: '22%', color: 'white' }}>
                    <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                        <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="120" fill="#5e3818" >{this.state.score2}</text>
                    </svg>
                </div>
                <img className='press-btn' alt='' src={continueBtn} style={{ height: '8.5%', bottom: '23%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToEndPage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Result2);
