import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
// import backgroundImage from '../assets/images/sample/07_第一關完成.png'
import backgroundImage from '../assets/images/result1/bg.jpg'
import nextBtn from '../assets/images/next.png'

class Result1 extends Component {

    btnSound = './sounds/btn_sound.mp3';
    completedSound = './sounds/completed.mp3';

    constructor(props) {
        super(props);
        this.state = {
            score: 0
        };
    }

    componentDidMount() {
        this.playCompletedSound();
        let score = (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score1;
        this.setState({
            score: score
        });
    }

    goToDesc2Page() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './desc2',
            state: {
                score1: this.state.score
            }
        });
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    playCompletedSound() {
        let btnAudio = new Audio(this.completedSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <div id='result-div' style={{ position: 'absolute', width: '100%', height: '100%', left: '0px', top: '0px' }}>
                    <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                </div>
                <div style={{ position: 'absolute', right: '36%', top: '30%', width: '20%', height: '22%', color: 'white' }}>
                    <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                        <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="120" fill="#5e3818" >{this.state.score}</text>
                    </svg>
                </div>
                <img className='press-btn' alt='' src={nextBtn} style={{ height: '8.5%', bottom: '23%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToDesc2Page.bind(this)}/>   
            </div>
        )
    }
}

export default withRouter(Result1);
