import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/sample/02_說明.png'
import backgroundImage1 from '../assets/images/game-intro/bg.jpg'
import nextBtn from '../assets/images/continue.png';

class GameIntro extends Component {

    btnSound = './sounds/btn_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            background: backgroundImage1,
            index: 0
        };
    }
    
    goToDesc1Page() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './desc1',
            state: {}
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.background} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {/* <img alt='' src={introImage} style={{ width: '60%', position: 'absolute', left: '2.5%', top: '4%' }}></img> */}
                <img className='press-btn' alt='' src={nextBtn} style={{ height: '8.5%', top: '69.5%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToDesc1Page.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(GameIntro);