import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/end/bg.jpg'
// import backgroundImage from '../assets/images/sample/10-結語.png'
import continueBtn from '../assets/images/continue.png'

class End extends Component {

    btnSound = './sounds/btn_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            score1: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score1,
            score2: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score2
        };
    }
    
    goToResultPage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './result',
            state: {
                score1: this.state.score1,
                score2: this.state.score2
            }
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {/* <img alt='' src={introImage} style={{ width: '60%', position: 'absolute', left: '2.5%', top: '4%' }}></img> */}
                <img className='press-btn' alt='' src={continueBtn} style={{ height: '8.5%', bottom: '23%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToResultPage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(End);