import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage1 from '../assets/images/desc2/bg1.jpg'
import backgroundImage2 from '../assets/images/desc2/bg2.jpg'
import startBtn from '../assets/images/start.png';
import continueBtn from '../assets/images/continue.png';

class Desc2 extends Component {

    btnSound = './sounds/btn_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            backgroundImage: backgroundImage1,
            score1: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score1
        }
    }

    continueBtnClick() {
        this.playBtnSound();
        this.setState({
            backgroundImage: backgroundImage2
        });
    }
    
    goToGame2Page() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './game2',
            state: {
                score1: this.state.score1
            }
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {
                    this.state.backgroundImage === backgroundImage1 && (<img className='press-btn' alt='' src={continueBtn} style={{ height: '8.5%', top: '69.5%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.continueBtnClick.bind(this)}/>)
                }
                {
                    this.state.backgroundImage === backgroundImage2 && (<img className='press-btn' alt='' src={startBtn} style={{ height: '8.5%', top: '69.5%', left: '43.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToGame2Page.bind(this)}/>)
                }
            </div>
        )
    }
}

export default withRouter(Desc2);
