import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import fetchTimeout from 'fetch-timeout';
import Cookies from 'universal-cookie';
import domtoimage from 'dom-to-image-improved';
import { saveAs } from 'file-saver';
import backgroundImage from '../assets/images/result/bg.jpg'
// import backgroundImage from '../assets/images/sample/11-成績畫面.png'
import downloadBtn from '../assets/images/result/download.png';
import introBtn from '../assets/images/result/intro.png';

import constant from '../assets/const.json';

const cookies = new Cookies();
const APP_NAME_PREFIX = "Forest-Rescue_";

class Result extends Component {

    btnSound = './sounds/btn_sound.mp3';
    winSound = './sounds/result.mp3';

    constructor(props) {
        super(props);
        this.state = {
            score1: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score1,
            score2: (this.props.location.state === null || this.props.location.state === undefined) ? 0 : this.props.location.state.score2
        };
    }

    componentDidMount() {
        let btnAudio = new Audio(this.winSound);
        btnAudio.play();
        let uid = cookies.get('uid');
        let score1 = this.state.score1;
        let score2 = this.state.score2;
        console.log('[Params] get uid from cookie = ' + uid);
        if (uid === null || uid === undefined) {
            uid = '';
            console.log('[Params] generate random uuid : ' + uid);
        }
        console.log('[Params] forest_score_1 = ' + score1 + ', forest_score_2 = ' + score2);
        this.postDataToServer(uid, score1, score2);
    }

    getRequestBody(uid, score1, score2) {
        return {
            uid: uid,
            forest_score_1: score1,
            forest_score_2: score2,
            action: 'result'
        };
    }

    postDataToServer(uid, score1, score2) {
        let apiPath = constant.WEB_API_PATH;
		fetchTimeout(apiPath, {
            headers: {
                'Content-Type': 'application/json'
            },
			method: 'POST',
			body: JSON.stringify(this.getRequestBody(uid, score1, score2))
        }, 5 * 1000, '[WebAPI] Timeout')
        .then(response => response.json())
		.then(data => {
			if (data.success) {
                // call api ok
                console.log('[WebAPI] success');
            } else {
                // call api failed
                console.log('[WebAPI] failed');
            }
		}).catch(e => {
			console.log('[WebAPI] ' + e);
		});
    }
    
    goToIntroPage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './intro',
            state: {}
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    downloadResultImage() {
        let element = document.getElementById('result-div');
        let scale = constant.DESIRED_RATIO_WIDTH / element.clientWidth;
        domtoimage.toBlob(element, {
            width: constant.DESIRED_RATIO_WIDTH,
            height: constant.DESIRED_RATIO_HEIGHT,
            style: {
                transform: 'scale(' + scale + ')',
                transformOrigin: 'top left'
            }
        })
        .then(function (blob) {
            const date = new Date();
            const mm = date.getMonth() + 1;
            const dd = date.getDate()
            const month = (mm > 9 ? '' : '0') + mm
            const day = (dd > 9 ? '' : '0') + dd
            const year = date.getFullYear();
            saveAs(blob, APP_NAME_PREFIX + year + month + day + '.png');
        });
    }

    render() {
        return (
            <div className='page-root-div'>
                <div id='result-div' style={{ position: 'absolute', width: '100%', height: '100%', left: '0px', top: '0px' }}>
                    <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                    {/* <img alt='' src={introImage} style={{ width: '60%', position: 'absolute', left: '2.5%', top: '4%' }}></img> */}
                    <div style={{ position: 'absolute', right: '33.5%', top: '36%', width: '11%', height: '6%', color: 'white' }}>
                        <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                            <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="120" fill="#5e3818" >{this.state.score1}</text>
                        </svg>
                    </div>
                    <div style={{ position: 'absolute', right: '33.5%', top: '45.5%', width: '11%', height: '6%', color: 'white' }}>
                        <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                            <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="120" fill="#5e3818" >{this.state.score2}</text>
                        </svg>
                    </div>
                    <div style={{ position: 'absolute', right: '32.5%', top: '56.2%', width: '14%', height: '7%', color: 'white' }}>
                        <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                            <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="120" fill="#eb551c" >{this.state.score1 + this.state.score2}</text>
                        </svg>
                    </div>
                </div>

                <img className='press-btn' alt='' src={downloadBtn} style={{ height: '8.5%', bottom: '23%', left: '34.5%', position: 'absolute', background: 'transparent' }} onClick={this.downloadResultImage.bind(this)}/>
                <img className='press-btn' alt='' src={introBtn} style={{ height: '8.5%', bottom: '23%', left: '50.5%', position: 'absolute', background: 'transparent' }} onClick={this.goToIntroPage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Result);