import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/intro/bg.jpg'
// import backgroundImage from '../assets/images/sample/14_科系介紹.png'
import learnSpaceBtn from '../assets/images/intro/study_btn.png'
import completedEndBtn from '../assets/images/intro/end.png'

import constant from '../assets/const.json';

class Intro extends Component {

    btnSound = './sounds/btn_sound.mp3';

    constructor(props) {
        super(props)
        this.state = {
            background: backgroundImage,
            index: 0
        };
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    goToHomePage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './',
            state: {}
        });
    }

    goToStudyAreaPage() {
        this.playBtnSound();
        window.top.location = constant.STUDY_AREA_URL
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.background} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img alt='' className='press-btn' src={learnSpaceBtn} style={{ position: 'absolute', height: '8.5%', right: '60%', bottom: '6.8%' }} onClick={this.goToStudyAreaPage.bind(this)}/>
                        <img alt='' className='press-btn' src={completedEndBtn} style={{ position: 'absolute', height: '8.5%', left: '58%', bottom: '6.8%' }} onClick={this.goToHomePage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Intro);
